import axios from 'axios';
import { useEffect, useReducer } from 'react';

const ACTIONS = {
    MAKE_REQUEST: 'make-request',
    GET_DATA: 'get-data',
    ERROR: 'error',
    UPDATE_HAS_NEXT_PAGE: 'update-has-next-page',
};

// const BASE_URL = 'https://jobs.github.com/positions.json';
const BASE_URL = 'https://cors-anywhere.herokuapp.com/https://jobs.github.com/positions.json';

function reducer(state, action) {
    switch (action.type) {
        case ACTIONS.MAKE_REQUEST:
            return { loading: true, jobs: [] };
        case ACTIONS.GET_DATA:
            return { ...state, loading: false, jobs: action.payload.jobs };
        case ACTIONS.ERROR:
            return { ...state, loading: false, error: action.payload.error, jobs: [] };
        case ACTIONS.UPDATE_HAS_NEXT_PAGE:
            return { ...state, hasNextPage: action.payload.hasNextPage };
        default:
            return state;
    }
}

function useFetchJobs(params, page) {
    const [state, dispatch] = useReducer(reducer, { jobs: {}, loading: true });

    useEffect(() => {
        const cancleToken1 = axios.CancelToken.source();
        const cancleToken2 = axios.CancelToken.source();

        dispatch({ type: ACTIONS.MAKE_REQUEST });
        // fetching Data
        axios
            .get(BASE_URL, {
                cancleToken: cancleToken1.token,
                params: { markdown: true, page, ...params },
            })
            .then((res) => {
                dispatch({ type: ACTIONS.GET_DATA, payload: { jobs: res.data } });
            })
            .catch((e) => {
                if (axios.isCancel(e)) return;
                dispatch({ type: ACTIONS.ERROR, payload: { error: e } });
            });
        // checking for next page
        axios
            .get(BASE_URL, {
                cancleToken: cancleToken2.token,
                params: { markdown: true, page: page + 1, ...params },
            })
            .then((res) => {
                dispatch({
                    type: ACTIONS.UPDATE_HAS_NEXT_PAGE,
                    payload: { hasNextPage: res.data.length !== 0 },
                });
            })
            .catch((e) => {
                if (axios.isCancel(e)) return;
                dispatch({ type: ACTIONS.ERROR, payload: { error: e } });
            });

        return () => {
            cancleToken1.cancel();
            cancleToken2.cancel();
        };
    }, [params, page]);

    return state;
}

export default useFetchJobs;
